@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    background-color: #1b65a6;
    height: auto;
    padding: 15px 0;
}

.header-menu{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

}

.header-Item {
    text-align: center;
    border: solid 1px rgba(30, 28, 63, 0.1);
    border-radius: 10px;
    color: #fff;
    padding: 10px;
    transition: all 1s;
    width: 200px;
}

.headerItem-icon{
    height: 15px;
}

.hidden {
    margin-right: 10%;
    display: none;
}
.hidden:hover {
    cursor: pointer;
    margin-right: 10%;
    opacity: 0.5;
}

.header-Item:hover{
    background-color: #1E1D40;
}



@media (max-width: 480px) {
    .header{
        display: flex;
        justify-content: flex-end;
        /* display: grid; */
    }

    .header-menu{
        display: flex;
        background-color: #1b65a6;
        left: 100%;
        overflow-y: auto;
        position: fixed;
        top: 50px;
        transition: left 0.3s;
        width: 100%;
    }

    .active {
        flex-direction: column;
        align-items: center;
        display: block;
        position: absolute;
        left: 0;
        top: 50px;
        z-index: 1;
    }

    .hidden {
        display: inline;
    }

    .header-Item {
        margin: 0 auto;
    }
    
    .hidden-img {
        -webkit-animation-duration: 0.5s;
                animation-duration: 0.5s;
        -webkit-animation-name: slidein;
                animation-name: slidein;
        margin-top: 40%;
        height: 20px;
    }
}



@-webkit-keyframes slidein {
    0% {
        opacity: 0;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }

    50% {
        opacity: 0.5;
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
    }
  
    100% {
        opacity: 1;
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
  }



@keyframes slidein {
    0% {
        opacity: 0;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }

    50% {
        opacity: 0.5;
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
    }
  
    100% {
        opacity: 1;
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
  }
.sideBar {
    background-color: #D90D32;
    color: #F7F6F2;
    display: flex;
    flex-direction: column;
    font-size: 17px;
    height: calc(100% - 100px);
    overflow-y: auto;
    padding: 0;
    position: absolute;
    width: 25%;
}

@media (max-width: 700px) {
    .sideBar {
        height: calc(100% - 150px);
        width: 100px;

    }
}

@media (max-width: 480px) {
    .sideBar {
        height: calc(100% - 96px);
        width: 100px;

    }
}
.sidebarElement {
    align-items: center;
    border: 0;
    border-radius: 30px;
    background: linear-gradient(145deg, #e80e36, #c30c2d);
    box-shadow:  7px 7px 14px #b60b2a,
                  -7px -7px 14px #fc0f3a;
    color: #F7F6F2;
    cursor: pointer; 
    display: flex;
    font-family: 'Poppins', sans-serif;
    justify-content: space-between;
    margin: 7px;
    padding: 20px;
    text-decoration: none;
    transition: all 1s;
}

.iconTeam {
    height: 24px;
}

.sidebarElement:hover {
    background: #1E1D40;
    /* Usar este efecto para una clase selecionada
    background: #D90D32;
    box-shadow: inset 7px 7px 14px #b60b2a,
                inset -7px -7px 14px #fc0f3a; */
}

@media (max-width: 700px) {
    .teamTitle{
        display: none;
    }
}
.score{
    border-radius: 50px;
    background: #F2E6D8;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border-radius: 20px;
    color: #000;
    text-align: center;
    margin: 10px;
    padding: 5px;
    width: 45%;
}

@media (max-width: 768px) {
    .score{
        width: 100%;
    }

}
.matches{
    color: #F7F6F2;
    display: flex;
    font-size: 20px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 25%;
    max-width: 75vw;
    padding: 10px 16px;
    width: calc(100% - 100px);
    height: 75vh;
    overflow: auto;
}

@media (max-width: 700px) {
    .matches{
        font-size: 15px;
        margin-left: 100px;
        max-width:100vw;
        height: 74vh;
        width: calc(100% - 100px);
    }

}

@media (max-width: 480px) {
    .matches{
        height: 85vh;
        margin-left: 100px;
        width: calc(100% - 100px);
    }

}
.loadingContainer{
    color: #F7F6F2;
    display: flex;
    font-size: 20px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 25%;
    padding: 10px 16px;
}

.scoreLoading{
    border-radius: 50px;
    background: #F2E6D8;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border-radius: 20px;
    color: #000;
    height: 150px;
    margin: 10px;
    padding: 50px;
    text-align: center;
    width: 45%;
}

.scoreLoading p {
    -webkit-animation-name: loadign;
            animation-name: loadign;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@media (max-width: 768px) {
    .scoreLoading{
        width: 100%;
    }
}

@-webkit-keyframes loadign {
    0% {
        opacity: 0;
    }
    25%{
        opacity: 0.4;
    }
    50%{
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
  }

@keyframes loadign {
    0% {
        opacity: 0;
    }
    25%{
        opacity: 0.4;
    }
    50%{
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
  }
.footer {
    text-align: right;
    background-color: #1B65A6;
    bottom: -10px;
    color: #fff;
    padding: 10px;
    position: fixed;
    width: 100%;
    height: 50px;
}

.footer p a {
    color: #1E1D40;
    text-decoration: none;
    padding-right: 10px;
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background-color: #F2E6D8;
    font-family: 'Poppins', sans-serif;
    max-height: 80vh;
}

::-webkit-scrollbar{
    width: 10px;
}

::-webkit-scrollbar-track{
    background: #1b65a6;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb{
    background: #1E1D40;
    border-radius: 10px;
}
